import React from "react";
import Grid from "@mui/material/Grid";
import { BrewsCard } from "./BrewsCard";

const BrewList = ({ list }) => {
  const renderList = () =>
    list.map(({ title, ort }, i) => (
      <Grid item key={title}>
        <BrewsCard id={i + 1} title={title} ort={ort} />
      </Grid>
    ));

  return (
    <Grid
      container
      direction="row"
      justifyContent="center"
      alignItems="flex-start"
      rowSpacing={1}
    >
      {list && renderList()}
      {/* {list &&
        list.map(({ title, ort }, i) => (
          <Grid item key={title}>
            <BrewsCard id={i + 1} title={title} ort={ort} />
          </Grid>
        ))} */}
    </Grid>
  );
};

export { BrewList };
